import { NextResponse, NextRequest } from "next/server";

export function middleware(req: NextRequest) {
  const token = req.cookies.get("next-auth.session-token"); // Adjust for your session

  if (!token) {
    return NextResponse.redirect(new URL("/signin", req.url));
  }

  return NextResponse.next();
}

// âœ… Ensure Next.js applies middleware to specific paths
export const config = {
  matcher: ["/dashboard/:path*"], // Apply to dashboard and subpages
};
